//normalize
@import "./partials/normalize";
//grid
@import "./partials/grid";
//fonts
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap&subset=cyrillic');

//colors
$colorA: #6dbf00;
$colorA2:#5da300;
$colorB: #008ddd;
$colorB2: #0073c4;
$colorC: #353535;
$colorD: #0c0c0c;
$colorE: #898d9b;
$colorF: #626262;
$colorG: #4f4f4f;
$colorH: #838383;
$colorI: #e2e3e8;
$colorJ: #ecedf0; 
$colorK: #d5d7df;
$colorL: #d6d8e0;
$colorM: #77787d;
$colorN: #3e3f41;
$colorO: #caccd7;
$colorP: #686868;


.colorA {
  color: $colorA;
}
.colorH {
  color: $colorH;
}
.colorG {
  color: $colorG;
}
.color-inherit {
  color: $colorC;
}
  
//transitions
$trs1: .15s ease-out;
$trs2: .3s ease-out;
$trs3: 1s ease-out;

//shadows
$shadow1: 0px 6px 18px 0px rgba(0, 0, 0, 0.15);

body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  min-width: 320px;
  line-height: 1.5;
  color: $colorC;
  background-color: $colorJ;
}

*::before,
*::after {
  box-sizing: inherit;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 1920px;
}

.content {
  flex: 1 1 100%;
  padding-top: 50px;
  padding-bottom: 100px;
}

.section {
  
}



